	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨读托克维尔的《旧制度与法国大革命》。过去看过他另一本重要著作《美国的民主》。这本书也是他有影响的著作，值得一读。</p>\n<p>发展论坛请X副市长来作报告，讲经济发展的形势。杨校长主持会议，说复旦大学已经请他作教授。X说，到复旦大学作教授比当副市长还感到荣誉，大家鼓了掌。他又说，要找一个上市公司来为复旦投资办高新技术，大家又热烈鼓掌。上海形势发展的六个新，如国民经济发展上了新台阶，改革开放有了新的发展，浦东有了新发展，产业结构有了新发展等等。</p>\n<p>上海现在财政在市政府的手里，说是叫十个锅子六个盖，谁需要盖就盖一下，钱不发下去，在有关地方滚，可以生利，去年盈利多亿。上海交通不行，要改变产业结构，不能再搞纺织业和机器制造，这样运输能力跟不上。要搞第三产业，主要是金融业，运钱很容易，也很轻。他最后讲了几个瓶颈的问题。报告十分精彩，很吸引人。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0013.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0013.md","frontmatter":{"title":"1月21日 星期五"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0013.md absPath of file >>> MarkdownRemark"}}

